.orangeBtn{
    width: 100px;
    height: 40px;
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 500;
    color: #F9EADF;
    background: url(../../../assets/images/button/2.png) no-repeat center top;
    background-size: 100%;
    line-height: 40px;
    border: 0px ;
    border-radius: 5px;
    outline:none;
}
.greenBtn{
    width: 100px;
    height: 40px;
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 500;
    color: #F9EADF;
    background: url(../../../assets/images/button/1.png) no-repeat center top;
    background-size: 100%;
    line-height: 40px;
    border: 0px ;
    border-radius: 5px;
    outline:none;
}

.orangeBtn:hover:active{
    border: 1px solid #AC5616 !important;
}
.greenBtn:hover:active{
    border: 1px solid #042C20 !important;
}
@media screen and (max-width: 350px) {
    .orangeBtn{
        width: 90px;
        height: 35px;
        font-size: 14px;
        font-family: Source Han Sans CN;
        font-weight: 500;
        color: #F9EADF;
        background: url(../../../assets/images/button/2.png) no-repeat center top;
        background-size: 100%;
        line-height: 35px;
        border-radius: 5px;
        outline:none;
    }
    .greenBtn{
        width: 90px;
        height: 35px;
        font-size: 14px;
        font-family: Source Han Sans CN;
        font-weight: 500;
        color: #F9EADF;
        background: url(../../../assets/images/button/1.png) no-repeat center top;
        background-size: 100%;
        line-height: 35px;
        border-radius: 5px;
        outline:none;
    }
}