@import '~antd/dist/antd.css';

/*常规屏幕,大于600px*/
body {
    color:#FFF;
}
.header-nav{
    width: 40%;
    height: 80px;
    line-height: 80px;
    text-align: center;
    margin: 0 auto;
}
.list-nav{
    font-size: 16px;
    color: #7F8B87;
    font-weight: 400;
}

.Wallet-img{
    width: 50px;
    height: 50px;
}

.section-div{
    width: 600px;
    min-height: auto;
    margin: 40px auto;
    /* border: 1px solid red; */
}
button:hover:active{
    border: 1px solid #115947;
    background-color: #115947;
    font-size: 14px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
/*火狐*/
input[type="number"] {
    -moz-appearance: textfield;
}

/*当屏幕尺寸小于600px*/
@media screen and (max-width: 600px) {
    body {
        color:lightblue;
    }
    .header-nav{
        width: 100%;
        height: 60px;
        line-height: 60px;
        text-align: center;
        padding-left: 0px;
    }
    .list-nav{
        font-size: 14px;
        color: #7F8B87;
        font-weight: 400;
    }
    .Wallet-img{
        width: 35px;
        height: 35px;
    }
    .section-div{
        width:  auto;
        height: auto;
        margin: 10px 20px;
        padding-bottom: 20px;
    }
}