.upgrade{
    width: 309px;
    height: 302px;
    background: url(../../../assets/images/nft/2.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    margin-top: 10px;
    position: relative;
}
.upgradeBtn{
    width: 100px;
    height: 100px;
    background: linear-gradient(0deg, #0D741C 0%, #0D6219 100%);
    border-radius: 50%;
    text-align: center;
    position: absolute;
    bottom: 10px;
    left: 88px;
    color: #D1E5E7;
    font-weight: bold;
    font-size: 20px;
    padding-top: 20px;
    cursor: pointer;
}
.upgradeBtn p{
    font-size: 14px;
    color: #FF8328;
    font-weight: 500;
}
.proteinSize{
    position: absolute;
    left: 18px;
    top: 20px;
    transform:scaleX(1.2) scaleY(1.2);
}
.proteinSize2{
    position: absolute;
    left: 208px;
    top: 75px;
    transform:scaleX(1.2) scaleY(1.2);
}
@media screen and (max-width: 600px) {
    .upgrade{
        width: 260px;
        height: 260px;
        background: url(../../../assets/images/nft/2.png) no-repeat center top;
        text-align: center;
        background-size: 100%;
        margin: 0 auto;
        margin-top: 5px;
        position: relative;
    }
    .upgradeBtn{
        width: 80px;
        height: 80px;
        background: linear-gradient(0deg, #0D741C 0%, #0D6219 100%);
        border-radius: 50%;
        text-align: center;
        position: absolute;
        bottom: 17px;
        left: 77px;
        color: #D1E5E7;
        font-weight: bold;
        font-size: 16px;
        padding-top: 16px;
        cursor: pointer;
    }
    .upgradeBtn p{
        font-size: 12px;
        color: #FF8328;
        font-weight: 500;
    }
    .proteinSize{
        position: absolute;
        left: 10px;
        top: 10px;
        transform:scaleX(1) scaleY(1);
    }
    .proteinSize2{
        position: absolute;
        left: 168px;
        top: 58px;
        transform:scaleX(1) scaleY(1);
    }

}
