.bg{
    width: 90%;
    height: auto;
    text-align: center;
    margin: 0 auto;
}
.bgFlex{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    margin: 0 auto;
}
.proteinColor{
    width: 33%;
    height: auto;
    margin-bottom: 15px;
}
.proteinName{
    width: 100%;
    height: 70px;
    padding-left: 20px;
    padding-top: 5px;
    transform:scaleX(1) scaleY(1);
}
.proteinCheck{
    width: 100px;
    height: 45px;
    background: url(../../../assets/images/nft/check.png) no-repeat center top;
    text-align: left;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 22px;
    text-align: center;
    padding-right: 18px;
    color: #CCF8D3;
    font-size: 12px;
}
.proteinUnCheck{
    width: 100px;
    height: 45px;
    background: url(../../../assets/images/nft/uncheck.png) no-repeat center top;
    text-align: left;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 20px;
    padding-left: 5px;
    color: #CCF8D3;
    font-size: 12px;
}
.pag{
    margin: 10px auto;
}
.unPagBtn{
    width: 60px;
    height: 28px;
    border: 1px solid #2F6B42;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 400;
    color: #3E6334;
    line-height: 14px;
    background-color: transparent;
    outline:none;
    text-align: center;
}
.pagBtn{
    width: 60px;
    height: 28px;
    border: 1px solid #2F6B42;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 400;
    color: #77A086;
    line-height: 14px;
    background-color: transparent;
    outline:none;
    text-align: center;
}
.upgradeBtn{
    width: 120px;
    height: 50px;
    font-size: 16px;
    font-weight: bold;
    color: #F9EADF;
    background: url(../../../assets/images/button/2.png) no-repeat center top;
    background-size: 100%;
    border: 0px ;
    border-radius: 5px;
    outline:none;
    margin-left: 45px;
    margin-right: 45px;

}
.upgradeBtn p{
    font-size: 12px;
}
.upgradeBtn:hover:active{
    border: 1px solid #AC5616 !important;
    background-color: #AC5616;
}


@media screen and (max-width: 600px) {
    .bgFlex{
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap:wrap;
        margin: 0 auto;
        margin-bottom: 20px;
    }
    .proteinColor{
        width: 33%;
        height: auto;
        margin-bottom: 10px;
    }
    .proteinName{
        width: 100%;
        height: 60px;
        padding-left: 11px;
        padding-top: 0px;
        transform:scaleX(0.85) scaleY(0.85);
    }
    .proteinCheck{
        width: 96px;
        height: 40px;
        background: url(../../../assets/images/nft/check.png) no-repeat center top;
        text-align: left;
        background-size: 100%;
        margin: 0 auto;
        padding-top: 18px;
        padding-left: 5px;
        color: #CCF8D3;
        font-size: 12px;
    }
    .proteinUnCheck{
        width: 96px;
        height: 40px;
        background: url(../../../assets/images/nft/uncheck.png) no-repeat center top;
        text-align: left;
        background-size: 100%;
        margin: 0 auto;
        padding-top: 18px;
        padding-left: 5px;
        color: #CCF8D3;
        font-size: 12px;
    }
    .upgradeBtn{
        width: 120px;
        height: 50px;
        font-size: 16px;
        font-weight: bold;
        color: #F9EADF;
        background: url(../../../assets/images/button/2.png) no-repeat center top;
        background-size: 100%;
        border: 0px ;
        border-radius: 5px;
        outline:none;
        margin-left: 5%;
        margin-right: 5%;
    }
    .upgradeBtn p{
        font-size: 10px;
    }

}
