.title{
    width: 360px;
    height: auto;
    border: 1px dashed #225030;
    border-radius: 10px;
    padding: 10px;
    margin: 0px auto;
    margin-bottom: 30px;
    font-size: 14px;
}
.method{
    color: #7A9182;
    margin-bottom: 5px;
}
.text{
    color: #7A9182;
}
.text span{
    color: #D46C17;
}
@media screen and (max-width: 600px) {
    .title{
        width: 96%;
        height: auto;
        border: 1px dashed #225030;
        border-radius: 10px;
        margin-bottom: 30px;
        margin: 0 auto;
        margin-bottom: 20px;
        font-size: 12px;
    }

}