.modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.65);
    z-index: 1000;
}
.bg{
    width: 350px;
    height: 430px;
    background: url(../../../assets/images/reward/1.png) no-repeat center top;
    background-size: 100%;
    margin: 200px auto;
}
.flexName{
    display: flex;
    flex-direction: row;
    padding: 40px 30px 0 30px;
}
.nameImage{
    width: 50%;
    height: 160px;
    border-bottom: 1px solid #194C33;
}
.nameData{
    width: 50%;
    padding-left: 20px;
    text-align: left;
    border-bottom: 1px solid #194C33;
}
.nameData h3{
    font-size: 16px;
    font-family: Source Han Sans CN;
    font-weight: 600;
    color: #EB9041;
}
.nameData p{
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #456E60;
    padding-bottom: 10px;
}
.nameData span{
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #CCF8D3;
}
.address{
    padding: 20px 30px 0 30px;
}
.address p{
    font-size: 14px;
    font-weight: 500;
    color: #456E60;
    padding-bottom: 15px;
    text-align: left;
}
.address input{
    width: 90%;
    height: 40px;
    background: #0C2C1C; 
    border-radius: 20px;
    border: 0px;
    outline:none;
    color: #236142;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 10px;
    text-align: center;

}
.flexBtn{
    width: 250px;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    padding-top: 30px;
}

.transferBtn{
    margin-left: 50px;
}

@media screen and (max-width: 600px) {
    .bg{
        width: 96%;
        height: 500px;
        background: url(../../../assets/images/reward/1.png) no-repeat center top;
        background-size: 100%;
        margin: 80px auto;
    }
}
@media screen and (max-width: 350px) {
    .bg{
        width: 90%;
        height: 500px;
        background: url(../../../assets/images/reward/1.png) no-repeat center top;
