.step{
    width: 350px;
    height: auto;
    border: 1px solid #225030;
    background: linear-gradient(0deg, #042C20 0%, #1D4126 100%);
    border-radius: 10px;
    margin:  0 auto;
    padding: 10px;
}
.stepTitle{
    width: 90%;
    font-size: 14px;
    font-weight: bold;
    color: #98AB9F;
    padding-left: 5px;
    margin-bottom: 10px;
}
.stepNumber{
    width: 90%;
    text-align: center;
    padding-left: 0px;
    text-align: left;
    padding-left: 10px;
}
.stepNumber h3{
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #98AB9F;
    line-height: 18px;
}
.stepNumber span{
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #DF7219;
    line-height: 18px;
}

.stepCompare{
    text-align: center;
    font-size: 16px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #DF7219;
}
.stepPool{
    font-size: 12px;
    font-weight: 400;
    color: #677F6F;
    text-align: center;
    padding-bottom: 20px;
}
.stepBtn{
    margin: 0 auto;
    width: 280px;
    padding-bottom: 5px;
}
.stepBtn button{
    width: 100px;
    height: 40px;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid #115947;
    color: #B9F3D9;
    border-radius: 20px;
    background-color: transparent;
    outline:none;
    margin-left: 20px;
    margin-right: 20px;
}
@media screen and (max-width: 600px) {
    .step{
        width: 100%;
        height: auto;
        border: 1px solid #225030;
        background: linear-gradient(0deg, #042C20 0%, #1D4126 100%);
        border-radius: 10px;
        margin:  0 auto;
        padding: 10px;
    }
}
@media screen and (max-width: 350px) {
    .step{
        width: 100%;
        height: auto;
        border: 1px solid #225030;
        background: linear-gradient(0deg, #042C20 0%, #1D4126 100%);
        border-radius: 10px;
        margin:  0 auto;
        padding: 10px;
    }
    .stepBtn{
        margin: 0 auto;
        width: 240px;
    }
    .stepBtn button{
        width: 100px;
        height: 40px;
        font-size: 14px;
        font-weight: 500;
        border: 1px solid #115947;
        color: #B9F3D9;
        border-radius: 20px;
        background-color: transparent;
        outline:none;
        margin-left: 10px;
        margin-right: 10px;
    }
}