.bg{
    width: 400px;
    height: 500px;
    background: url(../../assets/images/nft/1.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 10px;
}

.bgColor{
    width: 100%;
    height: 360px;
    background: url(../../assets/images/nft/7.png) no-repeat center top;
    background-size: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:center ;
    align-items:center ;
}
.solution{
    width: 70px;
    height: 70px;
    background: url(../../assets/images/nft/8.png) no-repeat center top;
    background-size: 100%;
}

.solutionCheck{
    width: 70px;
    height: 70px;
    background: url(../../assets/images/nft/8_check.png) no-repeat center top;
    background-size: 100%;
}
.buyImg{
    width: 383px;
    height: 180px;
    background: url(../../assets/images/nft/6.png) no-repeat center top;
    background-size: 100%;
    margin: 0 auto;
    margin-top: -50px;
}

.title{
    width: 400px;
    height: auto;
    border: 1px dashed #255131;
    color: #838C86;
    font-size: 14px;
    padding: 10px;
    margin: 0px auto;
    margin-bottom: 30px;
    border-radius: 5px;
    text-align: center;
}
.title .titleNumber{
    color: #D46C17;
}
