.title{
    width: 360px;
    height: auto;
    border: 1px solid #225030;
    background: linear-gradient(0deg, #042C20 0%, #1D4126 100%);
    border-radius: 10px;
    margin-bottom: 30px;
    margin: 0 auto;
    padding: 10px;
    font-size: 14px;
}
.method{
    color: #B0C2B6;
    margin-bottom: 5px;
}
.text{
    color: #7A9182;
}
@media screen and (max-width: 600px) {
    .title{
        width: 96%;
        height: auto;
        border: 1px dashed #225030;
        background: linear-gradient(0deg, #042C20 0%, #1D4126 100%);
        border-radius: 10px;
        margin: 0 auto;
        margin-bottom: 0px;
        font-size: 12px;
    }

}
