.bg{
    width: 400px;
    height: 500px;
    background: url(../../assets/images/nft/1.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 10px;
}

.highest{
    font-size: 16px;
    padding-top: 150px;
    color: coral;
    padding-left: 50px;
    padding-right: 50px;
}
.flexCheck{
    width: 300px;
    margin: 0 auto;
    display: flex;
    flex-direction : row;
    padding-left: 15px;
    padding-top: 10px;
}
.uncheck{
    width: 80px;
    height: 40px;
    border: 1px solid #1A4E34;
    background: linear-gradient(0deg, #042C20 0%, #1D4126 100%);
    border-radius: 5px;
    text-align: center;
    line-height: 40px;
    color: #77A086;
    font-size: 12px;
    cursor: pointer;
    margin: 0 auto;
}
.check{
    width: 80px;
    height: 40px;
    border: 1px solid #1A4E34;
    background: linear-gradient(0deg, #0A4229 0%, #2F7B42 100%);
    border-radius: 5px;
    line-height: 40px;
    color: #86C39B;
    font-size: 12px;
    cursor: pointer;
    margin: 0 auto;
}

