.flexCheck{
    width: 400px;
    margin: 0 auto;
    display: flex;
    flex-direction : row;
    margin-bottom: 20px;
}
.uncheck{
    width: 90px;
    height: 44px;
    background: url(../../assets/images/dig/1.png) no-repeat center top;
    text-align: center;
    line-height: 44px;
    background-size: 100%;
    color: #77A086;
    cursor: pointer;
    margin: 0 auto;
    font-size: 14px;
}
.check{
    width: 90px;
    height: 44px;
    background: url(../../assets/images/dig/2.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    line-height: 44px;
    color: #77A086;
    cursor: pointer;
    margin: 0 auto;
    font-size: 14px;
}
.flexPage{
    width: 400px;
    height: 300px;
    border: 1px solid yellow;
    margin:  0 auto;
    margin-top: 30px;
}
@media screen and (max-width: 600px) {
    .flexCheck{
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction : row;
        margin-bottom: 15px;
    }
    .uncheck{
        width: 80px;
        height: 40px;
        background: url(../../assets/images/dig/1.png) no-repeat center top;
        text-align: center;
        line-height: 40px;
        background-size: 100%;
        color: #77A086;
        cursor: pointer;
        margin: 0 auto;
        font-size: 12px;
    }
    .check{
        width: 80px;
        height: 40px;
        background: url(../../assets/images/dig/2.png) no-repeat center top;
        text-align: center;
        background-size: 100%;
        line-height: 40px;
        color: #77A086;
        cursor: pointer;
        margin: 0 auto;
        font-size: 12px;
    }

}