.upgrade{
    width: 309px;
    height: 302px;
    background: url(../../../assets/images/nft/4.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    margin-top: 10px;
    position: relative;
}
.upgradeBtn{
    width: 95px;
    height: 95px;
    background: linear-gradient(0deg, #0D741C 0%, #0D6219 100%);
    border-radius: 50%;
    text-align: center;
    position: absolute;
    bottom: 17px;
    left: 87px;
    color: #D1E5E7;
    font-weight: bold;
    font-size: 20px;
    padding-top: 20px;
    cursor: pointer;
}
.upgradeBtn p{
    font-size: 14px;
    color: #FF8328;
    font-weight: 500;
}
.proteinSize{
    position: absolute;
    left: 4px;
    top: 30px;
    transform:scaleX(0.75) scaleY(0.75);
}
.proteinSize2{
    position: absolute;
    left: 116px;
    top: 52px;
    transform:scaleX(0.75) scaleY(0.75);
}
.proteinSize3{
    position: absolute;
    right: 0px;
    top: 0px;
    transform:scaleX(0.75) scaleY(0.75);
}
.proteinSize4{
    position: absolute;
    right: 16px;
    top: 140px;
    transform:scaleX(0.75) scaleY(0.75);
}
