.bg{
    width: 350px;
    height: 280px;
    background: url(../../../../assets/images/dig/3.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 35px;
}
.icon{
    width: 20px;
    height: 20px;
    margin: 0 auto;
    background-color: #0C2C1C;
    border-radius: 20px;
    line-height: 20px;
}
.swap{
    font-size: 18px;
    font-weight: bold;
    color: #B9F3D9;
    margin-bottom: 10px;
}
.swapInput{
    width: 80%;
    height: 40px;
    background: #0C2C1C; 
    border-radius: 20px;
    border: 0px;
    outline:none;
    color: #236142;
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 10px;
    text-align: center;
    
}
input::-webkit-input-placeholder{
    color: #236142;
    font-size: 12px;
}
.balanceFlex{
    display: flex;
    flex-direction : row;
}
.balance{
    width: 50%;
    color: #16563A;
    font-size: 14px;
    text-align: center;
    font-weight: 400;
    border-right: 1px dashed #103924;;
}
.number{
    width: 50%;
    color: #16563A;
    font-size: 14px;
    text-align: center;
    font-weight: 400;
}
.btn{
    width: 100px;
    height: 40px;
    font-size: 12px;
    font-weight: 500;
    border: 1px solid #115947;
    color: #B9F3D9;
    border-radius: 20px;
    background-color: transparent;
    margin-top: 10px;
    outline:none;
}


@media screen and (max-width: 600px) {
    .bg{
        width: 100%;
        height: 280px;
        background: url(../../../../assets/images/dig/3.png) no-repeat center top;
        text-align: center;
        background-size: 100%;
        margin: 0 auto;
        padding-top: 20px;
    }
    .btn{
        width: 100px;
        height: 40px;
        font-size: 12px;
        font-weight: 500;
        border: 1px solid #115947;
        color: #B9F3D9;
        border-radius: 20px;
        background-color: transparent;
        margin-top: 5px;
        outline:none;
    }
}
@media screen and (max-width: 350px) {
    .swap{
        font-size: 16px;
        font-weight: bold;
        color: #B9F3D9;
        margin-bottom: 5px;
    }
    .swapInput{
        width: 80%;
        height: 35px;
        background: #0C2C1C; 
        border-radius: 20px;
        border: 0px;
        outline:none;
        color: #236142;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 5px;
        text-align: center;
    }
    .btn{
        width: 80px;
        height: 35px;
        font-size: 12px;
        font-weight: 500;
        border: 1px solid #115947;
        color: #B9F3D9;
        border-radius: 20px;
        background-color: transparent;
        margin-top: 5px;
        outline:none;
    }
    .balance{
        width: 50%;
        color: #16563A;
        font-size: 12px;
        text-align: center;
        font-weight: 400;
        border-right: 1px dashed #103924;;
    }
    .number{
        width: 50%;
        color: #16563A;
        font-size: 12px;
        text-align: center;
        font-weight: 400;
    }

}