.bg{
    width: 460px;
    height: 320px;
    background: linear-gradient(0deg, #032B20 0%, #1E4226 100%);
    border-radius: 20px;
    margin: 0px auto;
    padding-top: 40px;
    text-align: center;
    margin-top: 100px;
}
.bg h1{
    font-size: 26px;
    font-family: Source Han Sans CN;
    font-weight: 600;
    color: #E8FFEE;
    text-shadow: 0px 1px 2px rgba(35, 24, 21, 0.35);
}
.borderColor{
    width: 140px;
    height: 3px;
    background: #E8FFEE;
    margin:  0 auto;
    margin-bottom: 25px;
}
.bg p {
    width: 220px;
    font-size: 12px;
    font-weight: 400;
    color: #84B09A;
    margin:  0 auto;
    line-height: 25px;
}

.bg button{
    width: 200px;
    height: 50px;
    border: 0px;
    background: #FF8328;
    font-size: 22px;
    font-weight: 500;
    color: #FFF6F0;
    border-radius: 25px;
    outline: none;
    margin-top: 45px;
}