.modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.65);
    z-index: 1000;
}
.bg{
    width: 350px;
    height: 430px;
    background: url(../../../assets/images/reward/1.png) no-repeat center top;
    background-size: 100%;
    margin: 200px auto;
}
.trayBg{
    width: 325px;
    height: 160px;
    background: url(../../../assets/images/user/4.png) no-repeat center top;
    background-size: 80%;
    margin: 0 auto;
}
.trayName{
    text-align: center;
}
.trayName h3{
    font-size: 18px;
    font-weight: bold;
    color: #EB9041;
}
.trayName p{
    font-size: 14px;
    font-weight: 400;
    color: #456E60;
    line-height: 30px;
}
.trayName p span{
    color: #CCF8D3;
}
.flexBtn{
    width: 250px;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    padding-top: 30px;
}
.transferBtn{
    margin-left: 50px;
}
.trayText{
    margin:  0 auto;
    width: 80%;
    background: #04251A;
    padding: 5px;
    font-size: 12px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #456E60;
    margin-top: 15px;
}
@media screen and (max-width: 600px) {
    .bg{
        width: 96%;
        height: 500px;
        background: url(../../../assets/images/reward/1.png) no-repeat center top;
        background-size: 100%;
        margin: 80px auto;
    }
}
@media screen and (max-width: 350px) {
    .bg{
        width: 90%;
        height: 430px;
        background: url(../../../assets/images/reward/1.png) no-repeat center top;
        background-size: 100%;
        margin: 80px auto;
    }
    .trayBg{
        width: 200px;
        height: 130px;
        background: url(../../../assets/images/user/4.png) no-repeat center top;
        background-size: 100%;
        margin: 0 auto;
    }
    .trayName{
        text-align: center;
    }
    .trayName h3{
        font-size: 18px;
        font-weight: bold;
        color: #EB9041;
    }
    .trayName p{
        font-size: 14px;
        font-weight: 400;
        color: #456E60;
        line-height: 25px;
    }
    .trayName p span{
        color: #CCF8D3;
    }
    .flexBtn{
        width: 210px;
        display: flex;
        flex-direction: row;
        margin: 0 auto;
        padding-top: 10px;
    }
    .transferBtn{
        margin-left: 30px;
    }
    .trayText{
        margin:  0 auto;
        width: 80%;
        background: #04251A;
        padding: 5px;
        font-size: 10px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #456E60;
        margin-top: 10px;
    }
    
}