.title{
    width: 400px;
    height: auto;
    border: 1px dashed #255131;
    color: #838C86;
    font-size: 14px;
    padding: 10px;
    margin: 0px auto;
    margin-bottom: 30px;
    border-radius: 5px;
}
.title span{
    color: #D46C17;
}
@media screen and (max-width: 600px) {
    .title{
        width: 100%;
        height: auto;
        border: 1px dashed #255131;
        color: #838C86;
        font-size: 12px;
        padding: 10px;
        margin: 0px auto;
        margin-bottom: 15px;
        border-radius: 5px;
    }

}