.title{
    width: 360px;
    font-size: 16px;
    font-weight: 400;
    color: #98AB9F;
    line-height: 40px;
    margin: 0 auto;
    margin-bottom: 5px;
    margin-top: 20px;
    text-align: left;
}
@media screen and (max-width: 600px) {
    .title{
        width: 96%;
        font-size: 14px;
        font-weight: 400;
        color: #98AB9F;
        line-height: 40px;
        margin: 0 auto;
        text-align: left;
        margin-bottom: 0px;
    }
}