.bg{
    position: fixed;
    top: 100px;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.65);
    z-index: 1000;
}
@media screen and (max-width: 600px) {
    .bg{
        position: fixed;
        top: 70px;
        left: 0;
        right: 0;
        bottom: 0;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.65);
        z-index: 1000;
    }

}