.modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.65);
    z-index: 1000;
}
.bg{
    width: 350px;
    height: 430px;
    background: url(../../../assets/images/reward/1.png) no-repeat center top;
    background-size: 100%;
    margin: 200px auto;
    padding-top: 30px;
}
.solutionBg{
    width: 80px;
    height: 80px;
    background: url(../../../assets/images/nft/8_check.png) no-repeat center top;
    background-size: 100%;
    margin: 0 auto;
}
.solutionText{
    font-size: 14px;
    font-family: Source Han Sans CN;
    font-weight: 500;
    color: #ABD3B2;
    text-align: center;
    margin-top: 50px;
}
.solutionInput{
    width: 80%;
    margin:  0 auto;
    margin-top: 30px;
}
.solutionInput p{
    width: 90%;
    text-align: left;
    font-size: 12px;
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #98AB9F;
    margin: 0 auto;
    padding-top: 5px;
    padding-bottom: 10px;
}
.solutionInput input{
    width: 100%;
    height: 35px;
    background: #0C2C1C; 
    border-radius: 20px;
    border: 0px;
    outline:none;
    color: #236142;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 5px;
    text-align: center;
}
.flexBtn{
    width: 250px;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    padding-top: 30px;
}
.transferBtn{
    margin-left: 50px;
}
@media screen and (max-width: 600px) {
    .bg{
        width: 96%;
        height: 500px;
        background: url(../../../assets/images/reward/1.png) no-repeat center top;
        background-size: 100%;
        margin: 100px auto;
    }
}
