.title{
    width: 400px;
    height: auto;
    border: 1px dashed #255131;
    color: #838C86;
    font-size: 14px;
    padding: 10px;
    margin: 0px auto;
    margin-bottom: 30px;
    border-radius: 5px;
    text-align: center;
}
.title .titleNumber{
    color: #D46C17;
}
.bg{
    width: 400px;
    height: 500px;
    background: url(../../assets/images/nft/1.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 10px;
}
@media screen and (max-width: 600px) {
    .title{
        width: 100%;
        height: auto;
        border: 1px dashed #255131;
        color: #838C86;
        font-size: 14px;
        padding: 10px;
        margin: 0px auto;
        margin-bottom: 30px;
        border-radius: 5px;
        text-align: center;
    }
    .title .titleNumber{
        color: #D46C17;
    }
    .bg{
        width: 100%;
        height: 440px;
        background: url(../../assets/images/nft/1.png) no-repeat center top;
        text-align: center;
        background-size: 100%;
        margin: 0 auto;
        padding-top: 10px;
    }

}
@media screen and (max-width: 600px) {
