.bg{
    height: 443px;
    background: url(../../assets/images/reward/1.png) no-repeat center top;
    color: #B9F3D9;
    text-align: center;
    margin: 0px auto;
}
.title{
    text-align: center;
    font-size: 12px;
    font-weight: 400;
    color: #7A9182;
    padding-top: 35px;
    padding-bottom: 20px;
}
.qr{
    width: 200px;
    height: 200px;
    margin: 0px auto;
    background: #fff;
    padding-top: 10px;
    border: 10px solid #0C2C1C;
}
.address{
    width: 300px;
    height: auto;
    background: #0C2C1C;
    border-radius: 10px;
    margin: 0px auto;
    margin-top: 20px;
    font-size: 16px;
    font-weight: 400;
    color: #84AB98;
    word-wrap:break-word;
    padding: 5px;
}
.copyBtn{
    width: 100px;
    height: 40px;
    border: 1px solid #115947;
    border-radius: 20px;
    background-color: transparent;
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
    outline:none;
}
@media screen and (max-width: 400px) {
    .bg{
        height: 420px;
        background-size: 100%;
        color: #B9F3D9;
        margin-left: 0px;
    }
    .title{
        font-size: 12px;
        font-weight: 400;
        color: #7A9182;
        padding-top: 30px;
        padding-bottom: 10px;
    }
    .qr{
        width: 200px;
        height: 200px;
        margin: 0 auto;
        background: #fff;
        padding-top: 10px;
        border: 10px solid #0C2C1C;
    }
    .address{
        width: 90%;
        height: auto;
        background: #0C2C1C;
        border-radius: 10px;
        margin: 0 auto;
        margin-top: 10px;
        font-size: 12px;
        font-weight: 400;
        color: #84AB98;
        line-height: 40px;
    }
    .copyBtn{
        width: 100px;
        height: 40px;
        border: 1px solid #115947;
        border-radius: 20px;
        background-color: transparent;
        margin-right: 15px;
        margin-left: 15px;
        margin-top: 10px;
        outline:none;
        font-size: 12px;
    }
   
}
@media screen and (max-width: 350px) {
    .bg{
        height: 350px;
        background-size: 100%;
        color: #B9F3D9;
        margin-left: 0px;
        margin-bottom: 10px;
    }
    .title{
        font-size: 12px;
        font-weight: 400;
        color: #7A9182;
        padding-top: 20px;
        padding-bottom: 5px;
    }
    .address{
        width: 90%;
        height: auto;
        background: #0C2C1C;
        border-radius: 10px;
        margin: 0 auto;
        margin-top: 5px;
        font-size: 10px;
        font-weight: 400;
        color: #84AB98;
        line-height: 30px;
    }
    .copyBtn{
        width: 90px;
        height: 35px;
        border: 1px solid #115947;
        border-radius: 20px;
        background-color: transparent;
        margin-top: 5px;
        margin-left: 20px;
        margin-right: 20px;
        outline:none;
        font-size: 12px;
    }
    

}