
.bg{
    width: 360px;
    height: 115px;
    background: url(../../../assets/images/dig/4.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    padding-top: 30px;
    display: flex;
    flex-direction : row;
    padding: 10px;
}
.icon{
    width: 20%;
    height: 100%;
    line-height: 75px;
}
.icon img{
    width: 20px;
    height: 20px;
}
.swap{
    width: 50%;
    font-size: 16px;
    font-weight: bold;
    color: #B9F3D9;
    line-height: 79px;
    text-align: left;
}
.btn{
    width: 100px;
    height: 40px;
    color: #219D79;
    border: 1px solid #115947;
    border-radius: 20px;
    background-color: transparent;
    margin-top: 20px;
    margin-right: 10px;
    outline:none;
}
