.upgrade{
    width: 309px;
    height: 302px;
    background: url(../../../assets/images/nft/3.png) no-repeat center top;
    text-align: center;
    background-size: 100%;
    margin: 0 auto;
    margin-top: 10px;
    position: relative;
}
.upgradeBtn{
    width: 100px;
    height: 100px;
    background: linear-gradient(0deg, #0D741C 0%, #0D6219 100%);
    border-radius: 50%;
    text-align: center;
    position: absolute;
    bottom: 3px;
    left: 80px;
    color: #D1E5E7;
    font-weight: bold;
    font-size: 20px;
    padding-top: 20px;
    cursor: pointer;
}
.upgradeBtn p{
    font-size: 14px;
    color: #FF8328;
    font-weight: 500;
}
.proteinSize{
    position: absolute;
    left: 13px;
    top: 38px;
    transform:scaleX(0.85) scaleY(0.85);
}
.proteinSize2{
    position: absolute;
    left: 160px;
    top: 10px;
    transform:scaleX(0.85) scaleY(0.85);
}
.proteinSize3{
    position: absolute;
    left: 218px;
    top: 127px;
    transform:scaleX(0.85) scaleY(0.85);
}
